<template>
  <v-layout class="full-height" column align-center>
      <v-form v-model="isValid">
        <v-text-field
        color="#fb7600"
          label="Email"
          v-model="email"
          :rules="[notEmptyRule]"/>
        <v-text-field
        color="#fb7600"
          label="Password"
          type="password"
          v-model="password"
          :rules="[notEmptyRule]"/>
      </v-form>
        <v-btn 
          color="#fb7600"
          :disabled="!isValid"
          :dark="isValid"
          v-if="!isLoading"
          @click="signIn">
          Sign In
        </v-btn>
        <v-progress-circular
          v-else
          :size="70"
          :width="7"
          color="#fb7600"
          indeterminate
        />
        <div class="su-info">
          New to FlatRent? 
          <router-link to="signup"> 
            <span class="cta">SIGN UP</span>
            </router-link>
          </div>
  </v-layout>
</template>

<script>
  import store from "@/store";
  import { mapState } from "vuex";
  import { SIGN_IN } from "../vuex/actions.js";

  export default {
    name: "signin",
    data: function() {
      return {
        isValid: false,
        email: "",
        password: "",
        notEmptyRule: v => v.length > 0 || "Can't be empty"
      };
    },
    computed: mapState(["isLoading"]),
    methods: {
      signIn() {
        store.dispatch(SIGN_IN, {
          email: this.email,
          password: this.password
        });
      }
    }
  };
</script>

<style lang="sass" scoped>
  .full-height
    min-height: 100%
    justify-content: center
  .su-info
    margin-top: 5px
    font-size: 15px
    color: #3c3c3c
    .cta
      font-weight: 700
      color: #fb7600
      transition: opacity 0.2s
      &:hover
        opacity: 0.85
</style>
